
import { defineComponent, ref, nextTick } from "vue";
import { iCreateDialog } from "@/typings/component";
import { loading } from "@/utils";

export default defineComponent({
  name: "Fabric3dDialog",
  setup() {
    const visible = ref(false);
    const fabric3DContainer = ref<iCreateDialog | null>(null);

    const init = async () => {
      await nextTick();
      // @ts-ignore
      const garmentViewer = new window.TG3D.stylebook.Viewer({
        ele: fabric3DContainer.value
      });
      garmentViewer.clear3DModels();
      garmentViewer.showGarmentById("8zdEEn2Y");
      const loadingInstance = loading();
      // @ts-ignore
      window.TG3D.api.fabrics.get("6ZYX2zVJ").then((data: any) => {
        garmentViewer.changeMaterialToParts(["Primary"], data, () => {
          loadingInstance.close();
        });
      });
      // const tag = document.createElement("script");
      // tag.async = true;
      // tag.defer = true;
      // tag.src = "https://api.tg3ds.com/plugins/js";
      // tag.setAttribute("data-features", "stylebook,vot");
      // tag.setAttribute("data-key", "b8BnE9sSwdGGB54h2rWOyZXcZ5Urbg217SQt");
      // tag.setAttribute("data-host", "https://api.tg3ds.com");
      // console.log("tag", tag);
      // document.body.appendChild(tag);
      // const run = document.createElement('script')
      // run.text = `
      //
      // `
    };

    const open = (imageUrl: string) => {
      visible.value = true;
      init();
    };

    return {
      visible,
      fabric3DContainer,

      open
    };
  }
});
